body,
ul,
li,
h1,
p {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: #fafafa;
  color: #555;
  font-family: -apple-system, BlinkMacSystemFont, 'Helvetica Neue', sans-serif;
  line-height: 1.4;
}

.container {
  margin: 0 auto;
  max-width: 450px;
  padding: 15px;
}

.h1 {
  font-size: 24px;
  font-weight: 500;
  margin-top: 60px;
  padding-left: 48px;
}

.todo-list {
  background: white;
  border: 1px solid #e6e6e6;
  border-radius: 6px;
  box-shadow: 0 1px 3px rgba(0,0,0,.06);
  margin: 15px 0 150px;
}

.todo-list-item {
  display: flex;
  font-size: 19px;
  padding: 18px;
  width: 100%;

  + .add-todo,
  + .todo-list-item {
    border-top: 1px solid #e6e6e6
  }
}

.button-complete {
  background: #fff;
  border: 3px solid #e6e6e6;
  border-radius: 6px;
  cursor: pointer;
  flex: 0 0 auto;
  height: 18px;
  margin: 4px 12px 0 0;
  width: 18px;
}

.button-edit {
  color: #888;
  font-size: 15px;
  margin-left: auto;
  position: relative;
  top: 4px;
}

.input {
  background: none;
  border: none;
  color: inherit;
  line-height: inherit;
  outline: none;
  padding-left: 48px;
}

.notification {
  background: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 6px;
  border-width: 1px;
  box-shadow: 0 1px 3px rgba(0,0,0,.06);
  color: #555;
  display: flex;
  padding: 15px;
}

.emoji {
  font-size: 45px;
  margin-right: 12px;
}

.weight-strong {
  font-weight: 500;
}

.small {
  font-size: 13px;
}
